.about {
  width: 100%;
  height: 90vh;
  display: flex;
  flex-direction: column;
  margin-bottom: 650px;
}

.aboutTop {
  width: 100%;
  height: 45%;
}

.carousel-inner {
  display: flex;
  overflow: hidden;
}

.carousel-item {
  flex: 0 0 100%; /* Mi item chim 100% chiu rng */
  transition: transform 1s ease-in-out, opacity 0.5s ease-in-out; /* Thm hiu ng opacity */
  opacity: 0.2; /* Bt u vi trng thi m */
}

.carousel-item-next, .carousel-item-prev, .carousel-item.active {
  transform: translateX(0); /* iu chnh v tr nh */
}

.carousel-item-next, .carousel-item-prev {
  transform: translateX(100%); /* Di chuyn nh sang phi */
}

.carousel-item.active {
  opacity: 1; /* Hin th nh khi n tr thnh active */
}

.carousel img {
  width: 100%; /* Chiu ngang chim ton b mn hnh */
  height: 500px; /* Chiu cao c nh */
  object-fit: cover; /* m bo nh khng b mo, ct  ph hp */
}

.aboutBottom {
  width: 100%;
  height: 45%; 
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; 
  text-align: center; 
  margin-top: 50px;
  font-family: 'Playfair Display', serif; /* Elegant and high-class font */
  font-size: 1.2em; /* Adjust font size for readability */
  color: #333; /* Sophisticated dark color */
}


.aboutBottom h1 {
  font-size: 36px;
  font-family: 'Arial', sans-serif;
  font-weight: bold;
  color: #333; 
  margin-top: 20px; 
  margin-bottom: 20px; 
}

.aboutBottom p {
  font-size: 18px;
  line-height: 1.6;
  font-family: 'Open Sans', sans-serif;
  color: #555; 
  margin-bottom: 15px; 
  max-width: 800px; 
  text-align: justify; 
  margin-left: auto; 
  margin-right: auto; 
}
.aboutBottom strong {
  color: #f0a500; 
  font-size: 20px;
  font-family: ''Playfair Display'', serif;
}
