body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.setTablePage {
  background-image: url("../assets/background.jpg");
  /* ng dn n nh */
  background-size: cover;
  /* nh ph ton b trang */
  background-position: center;
  /* Cn gia nh */
  background-repeat: no-repeat;
  /* Khng lp li nh */
  min-height: 100vh;
  /* m bo bao ph ton b chiu cao mn hnh */
  display: flex;
  align-items: center;
  justify-content: center;
}

.setTableContainer {
  background-color: rgba(46, 59, 43, 0.904);
  width: 55%;
  margin: 60px auto;
  color: white;
  padding: 30px;
  border-radius: 30px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
}

h2 {
  text-align: center;
  font-family: 'Arial', sans-serif;
  font-size: 30px;
  margin-bottom: 10px;
}

/* S dng flexbox  chia form thnh 2 ct */
.setTableForm {
  display: grid;
  grid-template-columns: 1fr 1fr;
  /* 2 ct */
  gap: 80px;
  row-gap: 15px;
}

.inputGroup {
  display: flex;
  flex-direction: column;
}

.inputGroup label {
  font-size: 14px;
  margin-bottom: 5px;
}

.inputGroup input {
  padding: 10px;
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid #ccc;
  background-color: #f4f4f4;
  width: 100%;
}

.submitButton {
  background-color: #ff9f00;
  color: black;
  padding: 20px 20px; /* Tng padding ngang nu cn thit */
  border: none;
  font-size: 18px;
  font-weight: bold; /* Lm ch m */
  text-align: center; /* Cn gia ch theo chiu ngang */
  display: inline-flex; /* Dng inline-flex  nt c th t ng m rng */
  align-items: center; /* Cn gia theo chiu dc */
  justify-content: center; /* Cn gia theo chiu ngang */
  border-radius: 8px;
  transition: background-color 0.3s;
  grid-column: span 2;
  justify-self: center;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  margin-top: -10px;
  width: 70%; /* iu chnh chiu rng ca nt */
  height: auto; /* m bo nt khng b ko gin qu cao */
}


