.submitButton:hover {
  background-color: #ff7b00;
  font-size: 22px;
  font-weight: bold; /* Lm ch m */
}

.note {
  text-align: center;
  font-size: 15px;
  grid-column: span 2;
  /* Cn gia thng bo */
}

.note p {
  margin: 10px 0;
}

@media (max-width: 768px) {
  .setTableContainer {
    width: 95%;
    padding: 30px;
  }

  h2 {
    font-size: 20px;
  }

  .setTableForm {
    grid-template-columns: 1fr;
    /* Mt ct khi mn hnh nh */
  }
}