/* File: Food_Des.css */

/* Ton b container */
.foodDesContainer {
  padding: 20px;
  background-image: url("../assets/background.jpg"); /* ng dn n nh */
  background-size: cover; /* nh ph ton b trang */
  background-position: center; /* Cn gia nh */
  background-repeat: no-repeat; /* Khng lp li nh */
  background-color: rgba(255, 243, 243, 0.11); /* Mu trng vi  trong sut 50% */
  background-blend-mode: lighten; /* Pha trn mu nn vi nh */
}

/* Hnh nh mn n */
.foodImageSection {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(135deg, #89e746, #3cb043);
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 10px;
  margin: 0 60px; /* Thu hp khong cch vin tri v phi */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.foodImage {
  width: 500px; /* t chiu rng c nh */
  height: 300px; /* t chiu cao c nh */
  border-radius: 10px;
  object-fit: cover;
}

/* Thng tin mn n */
.foodInfoSection {
  text-align: left;
  padding: 10px 20px;
  background-color: #cff4b1;
  border: 1px solid #ddd;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.foodName {
  font-size: 34px;
  font-weight: bold;
  margin-bottom: 10px;
  color: #e8c00d;
}

.foodPrice {
  font-size: 18px;
  color: #28a745;
  margin-bottom: 20px;
  font-weight: 600;
}

/* Input s lng sn phm */
.input_number_product {
  display: flex; /* S dng flexbox  cn chnh */
  align-items: center; /* Cn gia theo chiu dc */
  justify-content: center; /* Cn gia theo chiu ngang */
  gap: 10px; /* Khong cch gia cc phn t */
  margin: 20px 0; /* Khong cch trn di */
}

.button_qty {
  width: 40px; /* Chiu rng c nh */
  height: 40px; /* Chiu cao c nh */
  background-color: #28a745; /* Mu nn xanh l */
  color: white; /* Mu ch trng */
  font-size: 18px; /* Kch thc ch */
  font-weight: bold; /* Ch m */
  border: none; /* Loi b vin */
  border-radius: 5px; /* Bo gc nh */
  cursor: pointer; /* Con tr chut dng pointer */
  display: flex; /* Cn gia ni dung bn trong nt */
  justify-content: center;
  align-items: center;
  transition: background-color 0.3s ease; /* Hiu ng hover */
}

.button_qty:hover {
  background-color: #218838; /* Mu nn khi hover */
}

.prd_quantity {
  width: 60px; /* Chiu rng ca  nhp */
  height: 40px; /* Chiu cao khp vi nt */
  text-align: center; /* Cn gia vn bn */
  font-size: 16px; /* Kch thc ch */
  padding: 5px; /* Padding bn trong  */
  border: 1px solid #ddd; /* Vin mu xm nht */
  border-radius: 5px; /* Bo gc nh */
  box-sizing: border-box; /* m bo padding khng lm thay i kch thc */
}


/* Nt thm vo gi hng */
.addToCartBtn {
  width: 100%;
  padding: 10px 20px;
  background-color: #f69e23;
  color: white;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.addToCartBtn:hover {
  background-color: #d49012;
}

/* M t mn n */
.foodDescription {
  margin-top: 30px;
  padding: 20px;
  background-color: #f4901d;
  border: 1px solid #dddddd;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 85%;
  margin-left: auto;
  margin-right: auto;
}

.foodDescription h3 {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 15px;
  color: #151515;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.188);
}

.foodDescription p {
  font-size: 16px;
  line-height: 2;
  color: #fff8f8;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.284);
}
