/* nh dng chung cho bng */
.card-body {
  margin: 20px 20px 70px 30px;
}

.table {
  width: 100%;
  margin: 20px 20px 0px 0px;
  border-collapse: collapse;
  max-width: 100%;
}

/* Thm vin cho bng */
.table,
.table th,
.table td {
  border: 1px solid #ddd;
  border-radius: 5px;
}

/* nh dng cc  u tin  cn tri */
.table th,
.table td {
  text-align: center;
}

.table th,
.table td {
  padding: 10px;
  /* Khong cch bn trong  */
  text-align: left;
  /* Cn tri ni dung */
  border: 1px solid #ddd;
  /* ng vin  */
}

.Category {
  display: flex;
  /* S dng Flexbox */
  align-items: center;
  /* Cn chnh cc phn t theo chiu dc */
  justify-content: space-between;
  /* m bo cc phn t c khong cch gia chng */
}

.Category button {
  margin-left: 10px;
  /* Thm khong cch gia title v button */
}

/* nh dng cho tiu  bng (thead) */
.table thead th,
.table thead th>button {
  background-color: #4CAF50;
  /* Mu nn tiu  */
  color: white;
  /* Mu ch tiu  */
  text-align: center;
  /* Cn gia tiu  */
  padding: 12px 15px;
  font-weight: bold;
  white-space: nowrap;
  /* Khng cho ch xung dng */
}


.table thead th:nth-child(1) {
  width: 5% !important;
}

.table thead th:nth-child(2) {
  width: 20% !important;
}

.table thead th:nth-child(3) {
  width: 10% !important;
}

.table thead th:nth-child(4) {
  width: 5% !important;
}

.table thead th:nth-child(5) {
  width: 45% !important;
}

.table thead th:nth-child(6) {
  width: 5% !important;
}

.table thead th:last-child {
  width: 5%;
}

/* nh dng cho ct STT */
.table tbody th[scope="row"] {
  background-color: #4CAF50;
  /* Mu nn tiu  */
  color: white;
  /* Mu ch tiu  */
  text-align: center;
  padding: 12px 15px;
  font-weight: bold;
}

/* Mu xen k cho cc dng */
.table tbody tr:nth-child(odd) {
  background-color: #d6fdd5;
}

.table tbody tr:nth-child(even) {
  background-color: #ffffff;
}

/* Hiu ng hover cho tng dng */
.table tbody tr:hover {
  background-color: #d1f9a3;
  /* Mu nn xanh l nht khi hover */
  transition: background-color 0.3s ease;
  /* Hiu ng chuyn mu mt */
}


.table>:not(caption)>*>* {
  background-color: unset;
}

.table tbody td svg {
  color: gray;
  cursor: pointer;
  margin-left: 40px;
}