.about {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    margin-bottom: 50px;
    background-color: rgb(250, 235, 219);
}
.carousel img {
    height: 500px;
    object-fit: cover;
}

.news-title {
  text-align: center; /* Cn gia tiu  */
  font-size: 60px; /* Kch thc ch */
  font-weight: bold; /* m tiu  */
  color: #f1b73a; /* Mu sc cho tiu  */
  margin-top: 30px;
  margin-bottom: 5px; /* Khong cch di tiu  */
  font-family: 'Cinzel', serif;
  text-shadow: 2px 2px 4px rgba(24, 24, 24, 0.185);
}

.news-section {
    text-align: center;
}

.card {
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.card:hover {
    transform: scale(1.05);
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.15);
}

.card-img-top {
    height: 200px;
    object-fit: cover;
}

.card-body {
    padding: 20px;
}

.btn-primary {
    background-color: #f8a941 !important;
    color: white !important;
    border: #ff66b2 !important;
    transition: background-color 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.btn-primary:hover {
    background-color: #fb9b1f  !important; /* Mu ti hn khi hover */
    transform: scale(1.05); /* Hiu ng zoom nh */
    color: white !important;
}

.news-marquee {
    width: 100%;
    overflow: hidden; /* n phn text vt ra ngoi */
  }
  
  .marquee-wrapper {
    display: flex; /* t cc on vn bn vo hng */
    animation: moveText 25s linear infinite;
  }
  
  .marquee-text {
    display: inline-block;
    white-space: nowrap; /* Ngn khng cho vn bn xung dng */
    margin-right: 50px; /* Khong cch gia cc on vn bn */
    color: rgb(31, 11, 11);
    background-image: linear-gradient(90deg, #fab941, #fdc620); 
    margin-top: 30px;
    font-family: 'Merriweather', serif;
    font-size: 50px;
  }
  
  
  .marquee-text:nth-child(1) {
    animation-delay: 0s; /* Dng 1 bt u ngay */
  }
  
  .marquee-text:nth-child(2) {
    animation-delay: 3s; /* Dng 2 bt u sau 3s */
  }

  .marquee-text:nth-child(3) {
    animation-delay: 6s; /* Dng 3 bt u sau 3s */
  }
  
  @keyframes moveText {
    0% {
      transform: translateX(100%); /* Bt u t bn tri */
      opacity: 1;
    }
    100% {
      transform: translateX(-100%); /* Chy ht ra bn phi */
      opacity: 1;
    }
  }
  