/* CSS cho .navbar */
.navbar {
  width: 100%;
  height: 100px;
  background-color: #121619;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
}

/* CSS cho .leftSide */
.navbar .leftSide {
  flex: 1;
  display: flex;
  align-items: center;
  padding-left: 50px;
}

.navbar .leftSide img {
  width: 100px;
}

/* CSS cho .rightSide */
.navbar .rightSide {
  flex: 1;
  display: flex;
  justify-content: center; /* Cn gia cc lin kt */
  align-items: center;
  gap: 20px;
}

.navbar a {
  text-decoration: none;
  color: rgb(255, 170, 72);
  font-size: 16px;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
  text-align: center;
}

.navbar a:hover {
  background-color: #f6a12a;
  color: white;
}

/* CSS cho .Icon */
.navbar .Icon {
  display: flex;
  justify-content: flex-end; /* m bo phn icon nm bn phi */
  align-items: center;
  gap: 20px;
}

.navbar .rightSide .Icon {
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 30px; /* Khong cch gia cc icon */
  margin-right: 60px;
}

.navbar .rightSide .Icon a {
  text-decoration: none;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  background: none;
  padding: 0;
  margin: 0;
}

.navbar .rightSide svg {
  font-size: 30px;
}

/* Media query cho thit b c chiu rng nh hn 900px */
@media only screen and (max-width: 900px) {
  .navbar .rightSide {
    justify-content: flex-start; /* iu chnh cn gia li khi mn hnh nh */
  }
  
  .navbar .rightSide a {
    font-size: 14px; /* Gim kch thc font ch cho mn hnh nh */
  }

  .navbar .rightSide .Icon a {
    font-size: 25px; /* Gim kch thc icon khi mn hnh nh */
  }
}

/* Media query cho thit b c chiu rng nh hn 600px */
@media only screen and (max-width: 600px) {
  .navbar .rightSide a {
    display: none; /* n cc lin kt khi mn hnh qu nh */
  }

  .navbar .rightSide {
    justify-content: flex-end;
    padding-right: 50px;
  }

  .navbar .Icon {
    gap: 15px; /* Gim khong cch gia cc icon */
  }
  
  .navbar .Icon a {
    font-size: 25px; /* Gim kch thc icon */
  }
}

.navbar .rightSide .links {
  margin-right: 200px; /* Li phn links sang tri 500px */
  display: flex;
  gap: 30px; /* Khong cch gia cc lin kt */
  align-items: center; /* Cn gia cc lin kt theo chiu dc */
  flex-wrap: nowrap; /* m bo cc lin kt khng xung dng */
}

.navbar .rightSide .links a {
  text-decoration: none;
  /* color: pink; */
  font-size: 16px;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
  text-align: center;
  white-space: nowrap; /* Ngn khng cho ch b xung dng */
}

.Icon .icon-wrapper {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: #fff; /* Ensure consistent icon color */
  background-color: transparent; /* No dark background */
  border-radius: 50%; /* Optional: for a circular container */
  padding: 5px; /* Adds spacing */
  transition: background-color 0.3s ease;
}

.Icon .icon-wrapper:hover {
  background-color: rgba(255, 255, 255, 0.2); /* Slight hover effect */
  color: #ffd700; /* Optional: Change color on hover */
}

.Icon .MuiSvgIcon-root {
  font-size: 24px; /* Ensure consistent size */
  color: inherit; /* Use inherited color */
}
