/* nh dng chung cho bng */
.card-body {
  margin: 20px 20px 70px 30px;
}

.table {
  width: 100%;
  margin: 20px 20px 0px 0px;
  border-collapse: collapse;
  max-width: 100%;
}

/* Thm vin cho bng */
.table,
.table th,
.table td {
  border: 1px solid #ddd;
  border-radius: 5px;
}

/* nh dng cc  u tin  cn tri */
.table th,
.table td {
  text-align: center;
}

.table th,
.table td {
  padding: 10px;
  /* Khong cch bn trong  */
  text-align: left;
  /* Cn tri ni dung */
  border: 1px solid #ddd;
  /* ng vin  */
}

.Category {
  display: flex;
  /* S dng Flexbox */
  align-items: center;
  /* Cn chnh cc phn t theo chiu dc */
  justify-content: space-between;
  /* m bo cc phn t c khong cch gia chng */
}

.Category button {
  margin-left: 10px;
  /* Thm khong cch gia title v button */
}

/* nh dng cho tiu  bng (thead) */
.table thead th,
.table thead th>button {
  background-color: #4CAF50;
  /* Mu nn tiu  */
  color: white;
  /* Mu ch tiu  */
  text-align: center;
  /* Cn gia tiu  */
  padding: 12px 15px;
  font-weight: bold;
  white-space: nowrap;
  /* Khng cho ch xung dng */
}


.table thead th:nth-child(1) {
  width: 5% !important;
}

.table thead th:nth-child(2) {
  width: 15% !important;
}

.table thead th:nth-child(3) {
  width: 5% !important;
}

.table thead th:nth-child(4) {
  width: 5% !important;
}

.table thead th:nth-child(5) {
  width: 5% !important;
}

.table thead th:nth-child(6) {
  width: 5% !important;
}

.table thead th:nth-child(7) {
  width: 35% !important;
}

.table thead th:nth-child(8) {
  width: 5% !important;
}
.table thead th:last-child {
  width: 5%;
}

/* nh dng cho ct STT */
.table tbody th[scope="row"] {
  background-color: #4CAF50;
  /* Mu nn tiu  */
  color: white;
  /* Mu ch tiu  */
  text-align: center;
  padding: 12px 15px;
  font-weight: bold;
}

/* Mu xen k cho cc dng */
.table tbody tr:nth-child(odd) {
  background-color: #d6fdd5;
}

.table tbody tr:nth-child(even) {
  background-color: #ffffff;
}

/* Hiu ng hover cho tng dng */
.table tbody tr:hover {
  background-color: #d1f9a3;
  /* Mu nn xanh l nht khi hover */
  transition: background-color 0.3s ease;
  /* Hiu ng chuyn mu mt */
}


.table>:not(caption)>*>* {
  background-color: unset;
}

.table tbody td svg {
  color: gray;
  cursor: pointer;
  margin-left: 20px;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.462);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 8px;
  width: 800px;
  height: 500px;
  max-height: 80vh;
  overflow-y: auto;
}

.tab-header {
  display: flex;
  justify-content: space-around;
  margin-bottom: 10px;
}

.tab-header button {
  padding: 10px 20px; /* iu chnh kch thc nt */
  cursor: pointer;
  border-radius: 8px; /* Bo gc mm mi hn */
  font-size: 16px; /* Tng kch thc ch */
  background-color: white; /* Mu nn mc nh */
  border: none; /* B vin */
  transition: all 0.3s ease; /* Hiu ng mt m */
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2); /* Bng nh ban u */
}

.tab-header button:hover {
  transform: translateY(-2px); /* Ni ln nh khi hover */
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3); /* Bng m hn */
}

.tab-header button:active {
  transform: translateY(1px); /* Nhn xung khi click */
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.4); /* Bng nh hn */
}

.tab-header button.active {
  font-weight: bold;
  color: white;
  background-color: rgb(255, 136, 0); /* Mu cam ni bt */
  box-shadow: 0px 4px 10px rgba(255, 136, 0, 0.5); /* Hiu ng bng cng tng mu */
}

.menu-item, .cart-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
}

h2 {
  font-weight: bold; /* In m */
  font-size: 30px; /* Tng kch thc */
  background: linear-gradient(45deg, #0e7a27, #4caf50, #a8e063); /* Gradient xanh l */
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent; /* n mu gc  hin gradient */
  display: inline-block;
  text-align: center;
  width: 100%; /* Cn gia theo chiu ngang */
  line-height: 1.2; /* Gi cho text khng b lch dng */
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.186); 
}


.menu-header {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr; /* Chia 3 ct vi  rng khc nhau */
  gap: 80px; /* Khong cch gia cc ct */
  margin-bottom: 10px; /* Khong cch gia tiu  v danh sch mn n */
  text-align: center; /* Cn gia cc tiu  */
}

.menu-header .menu-title, .menu-header .menu-price, .menu-header .menu-add {
  font-weight: bold;
  text-transform: uppercase; /* Vit hoa cc tiu  */
}



.menu-item {
  display: flex;
  justify-content: space-between; /* m bo cc phn t trong mi dng (tn, gi, nt) thng hng */
  align-items: center;
  padding: 10px 0; /* Khong cch gia cc dng */
  border-bottom: 1px solid #ddd; /* Thm ng vin di mi mn n */
  position: relative;
}

.dish-details {
  display: flex;
  flex-direction: row; /* Cc phn t con (tn mn n, gi) nm trn mt dng */
  align-items: center; /* Cn gia theo chiu dc */
  flex-grow: 1;
  font-size: 20px;
  margin-right: 130px;
  justify-content: space-between; 
}

.dish-name {
  margin-right: 15px; /* Khong cch gia tn mn n v gi */
  text-align: left; /* Cn l tri */
  width: 150px;
}

.dish-price {
  color: rgb(234, 34, 34);
  text-align: left;
  width: 120px;
  font-weight: 600;
}

.dish-img {
  width: 95px; /* iu chnh kch thc nh */
  height: 80px;
  margin-right: 0px; /* Khong cch gia nh v thng tin */
  border-radius: 10px;
}

.menu-item svg {
  font-size: 30px; /* iu chnh kch thc icon */
  cursor: pointer;
  transition: transform 0.2s ease;
  margin-right: 70px;
}

.menu-item svg:hover {
  transform: scale(1.2);
}

