.menu-header-cart {
  display: grid;
  grid-template-columns: 3fr 1fr 1fr 1fr; /* Chia ct cho cc tiu  */
  gap: 20px; /* Khong cch gia cc ct */
  justify-content: space-between;
  align-items: center; /* Cn gia cc mc theo chiu dc */
  padding: 0px 50px;
  font-weight: bold;
  border-bottom: 1px solid #ccc;
  text-align: center; /* Cn gia tt c ni dung trong cc ct */
}

.menu-header-cart span {
  font-size: 16px; /* iu chnh kch thc ch */
  text-align: center; /* Cn gia ni dung trong cc ct */
}

.menu-title-cart {
  text-align: left; /* Cn l tri cho "Mn n" */
  padding-left: 10px; /* m tri cho "Mn n" */
}

.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid #ddd;
}

.dish-details-cart {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.dish-name-cart {
  margin-left: 0px; 
  margin-right: 50px;
  text-align: left; /* Cn l tri */
  width: 110px;
  flex-grow: 1;
}

.dish-price-cart {
  text-align: left;
  width: 120px;
  font-weight: 600;
  margin-right: 180px;
  margin-left: -15px;
}

.cart-item-controls {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-left: -195px;
}

.cart-item-controls button {
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  padding: 5px 10px;
  cursor: pointer;
  font-size: 16px;
  border-radius: 5px;
}

.cart-item-controls button:hover {
  background-color: #ddd;
}

.cart-item-controls span {
  font-size: 16px;
  font-weight: bold;
}

.dish-total {
  font-weight: bold;
  font-size: 16px;
  color: #d9534f;  /* Mu  ni bt */
  margin-right: 50px;
  margin-left: 5px;
}

h3 {
  font-size: 20px;
  font-weight: bold;
  color: #333;
  margin-top: 20px;
}

/* nh dng cho nt thanh ton */
.btn-success {
  background-color: #28a745;  /* Mu xanh l */
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.btn-success:hover {
  background-color: #218838;  /* Mu xanh l m khi hover */
}

.btn-danger {
  background-color: #dc3545;  /* Mu  */
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.btn-danger:hover {
  background-color: #c82333;  /* Mu  m khi hover */
}